import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue'
import uploadImage from '@/components/common/upload-image.vue';
export default {
	components: {
		uniNavBar,
		uploadImage
	},
	data() {
		return {
			content: '',
			imgArr: [],
			showToast: false
		}
	},
	/* onBackPress(e) {}, */
	onLoad(){
		uni.getStorage({
			'key': 'add-input',
			success: (res) => {
				res = JSON.parse(res.data);
				console.log(res);
				this.content = res.con; 
				this.imgArr = res.img; 
			}
		})
	},
	//监听返回
	onBackPress() {
		console.log(this.content)
		if((this.content !== '' || this.imgArr.length > 0) && !this.showToast){
			uni.showModal({
				title: '提示',
				content: '是否保存为草稿',
				showCancel: true,
				cancelText: '不保存',
				confirmText: '保存',
				success: (res) => {
					if (res.confirm) {
						this.store();
					}else if(res.cancel) {
						uni.removeStorage({
							key: 'add-input'
						})
					}
					uni.navigateBack({delta: 1 })
				}
			});
			this.showToast = true;
			return true;
		}
	},
	computed: {
		imgElIsShow(){
			return this.imgArr.length > 0;
		}
	},
	methods: {
		//返回事件
		clickLeft(){
			uni.navigateBack({delta: 1 });
		},
		//获取uploadImage子组件中的imgArr
		checkedImage(imgArr){
			this.imgArr = imgArr;
		},
		//将内容存储在本地
		store(){
			uni.setStorage({
				key: 'add-input',
				data: JSON.stringify({
					'con': this.content,
					'img': this.imgArr
				}),
				success: function(){
				}
			})
		},
		//点击底部操作栏的第三个icon - 添加图片
		clickIcon3(){
			this.$refs.uploadImageEl.chooseImage();
		}
	}
}
